.container{
    max-width: 100vw;
    max-height: 100;
    
}
.heading{
    font-size: 3.5rem;
    color: brown;
    color: #CCCCCC;
    text-shadow: 0 1px 0 #999999, 0 2px 0 #888888,
             0 3px 0 #777777, 0 4px 0 #666666,
             0 5px 0 #555555, 0 6px 0 #444444,
             0 7px 0 #333333, 0 8px 7px rgba(0, 0, 0, 0.4),
             0 9px 10px rgba(0, 0, 0, 0.2);
}

.weather{
    width: 380px;
    margin-top: 10vh;
    padding: 30px;
    border-radius: 10px;
    margin-left: auto;
    margin-right: auto;
    background: linear-gradient(130deg,rgb(41, 41, 128),rgb(153, 153, 182));
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.29);
    
}

.search{
    display: flex;
    align-items: center;
    justify-content: space-between;
} 

.search input{
    border: none;
    outline: 0;
    padding: 12px 25px;
    font-size: 18px;
    background-color: rgb(222, 237, 243);
    color: black;
    border-radius: 30px;
    flex: 1;
    margin-right: 12px;
}

.search button{
    border: none;
    outline: 0;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
}

.search button img{
    width: 30px;
}

.winfo .icon{
    margin-top: 20px;
    width: 170px;
}
.winfo h1{
    font-size: 70px;
    font-weight: 500;
    margin-top: 5px;
}

.winfo h2{
    font-size: 40px;
    font-weight: 400;
    margin-top: -40px;
}

.details{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 20px;
    margin-top: 50px;
}

.details .col{
    display: flex;
    align-items: center;
    text-align: left;
}

.details .col img{
    width: 45px;
    margin-right: 10px;
}

.humidity, .wind{
    font-size: 27px;
}

.humidity p, .wind p{
    margin: 0px;
}